

















import {
	mapState,
	mapMutations
} from 'vuex'

export default {
	data() {
		return {
			title: 'login',
			providerList: []
		}
	},
	computed: {
		...mapState(['hasLogin'])
	},
	onLoad() {
		uni.getProvider({
			service: "oauth",
			success: (e) => {
				this.providerList = e.provider.map((value) => {
					switch (value) {
						case 'weixin':
							return {
								name: '微信登录',
								id: value
							}
						case 'qq':
							return {
								name: 'QQ登录',
								id: value
							}
						case 'sinaweibo':
							return {
								name: '新浪微博登录',
								id: value
							}
						case 'xiaomi':
							return {
								name: '小米登录',
								id: value
							}
					}
				})
			},
			fail: (e) => {
				console.log("获取登录通道失败", e);
			}
		});
	},
	methods: {
		...mapMutations(['login']),
		bindLogin(e) {
			uni.login({
				provider: e.id,
				success: (res) => {
					console.log("登录", res);
					this.login(e.id); //改变保存在store里的登录状态
				},
				fail: (e) => {
					console.log("fail", e);
				}
			})
		}
	}
}



